.button {
  color: #444444;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.1;
  text-transform: uppercase;
  text-align: center;
  padding-top: 1.11111em;
  padding-bottom: 1.1111em;
  border: 2px solid #444444;
  background-color: #ffffff;
}
.button-hover,
.button:hover {
  color: #ffffff;
  background-color: #444444;
}
.button-focus,
.button:focus {
  color: #ffffff;
  background-color: #444444;
}
.button-active,
.button:active {
  color: rgba(255, 255, 255, 0.5);
  background-color: #444444;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.modal {
  display: none;
}
.modal__title {
  margin: 0 auto;
  padding: 0;
  width: 60%;
  text-align: center;
  text-transform: uppercase;
}
.modal__form {
  width: 100%;
