  padding-right: 1.5em;
  border: 2px solid rgba(68, 68, 68, 0.1);
  background-color: #ffffff;
}
.modal__form-input:first-child:checked ~ .modal__form-label[for="S"],
.modal__form-input:nth-of-type(2):checked ~ .modal__form-label[for="M"],
.modal__form-input:nth-of-type(3):checked ~ .modal__form-label[for="L"] {
